/* Анимация открытия/закрытия реализована только на desktop из соображений производительности. */

.modal_theme_normal {
    display: none;

    background: rgba(50, 50, 50, .2);
}

.modal_theme_normal.modal_visible_yes {
    display: block;
}

.modal_theme_normal .modal__content {
    margin: 5px;

    background: #fff;
}
