.tooltip__close {
    position: absolute;
    z-index: 21;
    top: 9px;
    right: 9px;

    width: 16px;
    height: 16px;

    opacity: .25;

    transition: opacity .1s;
    transform: translateZ(0);

    /* background: url() лежит в _theme_promo */
}

.tooltip__close:hover {
    opacity: .7;
}
