/* Необходимо перебить обычный размер границ */
.textinput.textinput_type_number .textinput__control,
.textinput.textinput_type_number.textinput_has-clear_yes .textinput__control {
    border-right-width: 2px;
}

.textinput_size_xs.textinput_type_number.textinput_has-clear_yes .textinput__clear_visible_yes~.textinput__control {
    margin-right: -22px; /* Чтобы размеры инпута не изменялись при появлении крестика */
    border-right-width: 24px; /* на размер картинки */
}

.textinput_size_xs.textinput_type_number.textinput_has-clear_yes.textinput_icon-right_yes .textinput__clear_visible_yes~.textinput__control {
    margin-right: -19px; /* 43 - 24 */
    border-right-width: 43px; /* 24 + 24 - наложение картинок (5px) */
}

.textinput_size_xs.textinput_has-clear_yes.textinput_type_number.textinput_icon-right_yes .textinput__control {
    border-right-width: 24px; /* Чтобы нативные стрелочки _type_number не сильно отступали от иконки */
}

