.yc-root.yc-root_theme_light .ch-version-select {
    margin-left: 31px;
    color: var(--disable-color-text);
    background: var(--dark-grey);
    width: 160px;

    &__popup::before {
        background: var(--dark-grey);
        color: var(--disable-color-text);
    }

    &__popup {
        color: var(--disable-color-text);
        border-color: var(--dark-grey);
        padding: 0;

        & .yc-select-item:hover {
            background: var(--dark-grey-2);
        }
    }
}
